img {
  display: block;
  width: 100%;
}

.hero {
  padding: 40px 0px 28px;
  min-width: 320px;
  height: 380px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;

  @include tablet {
    background-position-x: 120px;
  }

  @include tablet {
    width: 768px;
    height: 432px;
    margin-left: auto;
    margin-right: auto;
  }
  @include desktop {
    width: 1280px;
    height: 720px;
  }
}

.hero-img {
  height: 380px;
  position: absolute;
  background-repeat: no-repeat;
  background-size: cover;

  @include tablet {
    width: 482px;
    height: 432px;
    margin-left: auto;
    margin-right: auto;
  }
  @include desktop {
    width: 1280px;
    height: 720px;
  }
}

.black-span {
  opacity: 100%;
  display: flex;
  @include tablet {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    background-image: url(../images/hero/hero-desktop-3x.png);
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.hero-wrap__content {
  max-width: 295px;
  height: 182px;
  margin-left: 20px;

  @include tablet {
    margin-top: 36px;
    margin-left: 32;
    max-width: 295px;
  }

  @include desktop {
    margin-top: 118px;

    max-width: 385px;
  }
}

.title {
  width: 100%;
  font-weight: 700;
  font-size: 32px;
  line-height: 38px;

  color: #ffffff;

  margin-bottom: 12px;

  @include tablet {
    width: 265px;

    font-size: 30px;
    line-height: 31px;
  }

  @include desktop {
    width: 385px;

    font-weight: 700;
    font-size: 64px;
    line-height: 75px;
  }
}

.catalog__stars-wrap-hero {
  margin-right: 0px;
  width: 110px;
  font-size: 18px;
  position: relative;
  padding-bottom: 20px;

  &::before {
    display: block;
  }
}

.catalog__rating-active {
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;

  &::before {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    color: rgb(243, 152, 54);
  }
}

.description {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;

  color: #f8f8f8;
  margin-top: 16px;
  margin-bottom: 20px;

  @include tablet {
    margin-top: 20px;
    margin-bottom: 28px;
    width: 260px;
  }
  @include desktop {
    margin-top: px;

    width: 450px;
    font-size: 16px;
    line-height: 19px;
  }
}

.watch-trailer__btn {
  padding: 12px 24px;

  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: 1.4;

  border: 1px solid var(--brend-color);
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border-radius: 74px;
  color: #111111;
  cursor: pointer;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.watch-trailer__btn:hover,
.watch-trailer__btn:focus {
  color: var(--brend-color);
  background: #fff;
  border: 1px solid var(--brend-color);
}
